@import url(https://fonts.googleapis.com/css?family=Montserrat);
@import url(https://fonts.googleapis.com/css?family=Staatliches);
@import url(https://fonts.googleapis.com/css?family=Fredoka+One);
@import url(https://fonts.googleapis.com/css?family=Poiret+One);
@import url(https://fonts.googleapis.com/earlyaccess/cwtexyen.css);
@import url(https://fonts.googleapis.com/css?family=Julius+Sans+One);
@import url(https://fonts.googleapis.com/css?family=Noto+Sans+TC:400,700);
@import url(https://fonts.googleapis.com/css?family=Kosugi+Maru);
@import url(https://fonts.googleapis.com/css?family=M+PLUS+Rounded+1c:500);
@import url(https://fonts.googleapis.com/css?family=Monoton);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  color: #111111;
  /* font-family: "Julius Sans One", "Monoton", "Poiret One", "Fredoka One",
    "Staatliches", "Montserrat", "Noto Sans TC", "cwTeXYen", sans-serif; */
  font-family: "Montserrat", "Noto Sans TC", sans-serif;
  padding: 0;
  margin: 0;
  font-size: 14px;
}
body {
  background: #f9f9f9;
}
a {
  color: #111111;
  text-decoration: none;
}
a:hover {
  color: #111111;
  text-decoration: none;
}
a:active {
  color: #111111;
  text-decoration: none;
}
a:focus {
  color: #111111;
  text-decoration: none;
}
ul li {
  list-style: none;
}
img {
  max-width: 100%;
}
h1 {
  font-size: 20px;
  font-weight: bold;
}
h2 {
  font-size: 24px;
}
button {
  background: #111111;
  border: 0;
  outline: 0;
  color: white;
}

